$backgroundPrimary: #2f005b;
$accent: #5600a8;
$font-primary: 'Avenir';
$accentNeutral: #e1d7ed;
$fullGrid: 1 / 1 / -1 / -1;
$glitch-width: 40vmax;
$glitch-height: calc(40vmax * 1.25);
$gap-vertical: 2px;
$gap-horizontal: 20px;
$time-anim: 2.25s;
$blend-mode-1: none;
$blend-mode-2: none;
$blend-mode-3: none;
$blend-mode-4: none;
$blend-mode-5: none;
$blend-color-1: transparent;
$blend-color-2: transparent;
$blend-color-3: transparent;
$blend-color-4: transparent;
$blend-color-5: transparent;
$text-shadow: 0 1px 0 hsla(271, 100%, 30%, 1),
    0 2px 0 hsla(271, 100%, 29%, 1),
    0 3px 0 hsla(271, 100%, 28%, 1),
    0 4px 0 hsla(271, 100%, 26%, 1),
    0 5px 0 hsla(271, 100%, 24%, 1),
    0 6px 0 hsla(271, 100%, 22%, 1),
    0 7px 0 hsla(271, 100%, 20%, 1),
    0 8px 0 hsla(271, 100%, 18%, 1),
    0 9px 0 hsla(271, 100%, 15%, 1),
    0 0 5px rgba(0, 0, 0, .05),
    0 1px 3px rgba(0, 0, 0, .2),
    0 3px 5px rgba(0, 0, 0, .2),
    0 5px 10px rgba(0, 0, 0, .2), 
    0 10px 10px rgba(0, 0, 0, .2),
    0 20px 20px rgba(0, 0, 0, .3);


body {
	background-color: black;
	transition: background .4s ease-in;
}

.header {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	box-sizing: border-box;
	width: 100%;
	background-color: black;
	transition: background .4s ease-in;
}

@keyframes slide1{
	0% {
		transform: translateY(-200px);
		opacity: 0;
		display: none;

	}
	100% {
		transform: translate(0);
		opacity: 1;
	}
}



.header-inner {
	display: flex;
	flex-direction: row;
	box-sizing: border-box;
	align-items: center;
	text-align: center;
	justify-content: center;
	padding-top: 20px;
	z-index: -1;
}

.header-inner a:nth-child(1) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(2) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(3) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(4) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(5) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a {
	text-transform: uppercase;
	font-size: 12px;
	font-weight: 700;
	text-decoration: none;
	color: $accent;
	padding-top: 15px;
	padding-bottom: 15px;
	padding: 10px;
	font-family: $font-primary;
}

.header-inner a:hover, {
	color: $accentNeutral;
	letter-spacing: 5px;
}

.header-inner a {
	transition: all 0.6s;
}

@keyframes appear {
	0% {
		opacity: 0;
		transform: scale(.2);
	}

	100% {
		opacity: 1;
	}
}

.hero-text {
	animation: 1.2s ease-in-out 0s 1 appear;
	position: relative;
	text-align: center;
	font-family: $font-primary;
	color: $accent;
	text-shadow: $text-shadow;
	font-size: 180px;
	font-weight: 900;
	padding-top: 220px;
	padding-bottom: 220px;
	margin: 0 auto;
	width: 100%;
	letter-spacing: 5px;
	text-transform: uppercase;
	z-index: -2;
}

.grid {
	margin: 50vh auto 0;
	position: relative;
	width: 100%;
	display: grid;
	max-width: 1200px;
	grid-template-columns: repeat(2, 1fr);
}

.grid__item {
	margin: 0 0 40vh;
	position: relative;
}

.grid__item:nth-child(odd) {
	margin-top: -40vh;
}

.grid__item-title {
	position: absolute;
	text-shadow: $text-shadow;
	opacity: 0;
	margin: 0;
	text-transform: lowercase;
	font-size: 6vw;
	color: $accent;
	font-family: "futura", sans-serif;
	font-weight: 800;
	padding: 30vh 0.5em 0;
	top: 0;
	pointer-events: none;
	line-height: 1;
}

.grid__item:nth-child(odd) .grid__item-title {
	right: 0;
	text-align: right;
	padding-top: 10vh;
}

.grid__item-title span {
	display: block;
	position: relative;
	padding-top: 10px;
	font-size: 20%;
	font-weight: 500;
	opacity: 0;
	color: white;
	text-shadow: none;
	text-transform: uppercase;
	letter-spacing: 5px;

}

.glitch:hover + .grid__item-title span {
	opacity: 1;
	animation: glitch-anim-text 0.5s linear;
}

.grid__item:nth-child(odd):hover .grid__item-title {
	opacity: 1;
	animation: glitch-title-2 0.5s linear;
}

.grid__item:nth-child(even):hover .grid__item-title {
	opacity: 1;
	animation: glitch-title 0.5s linear;
}



@media screen and (max-width: 55em) {
	.grid { 
		grid-template-columns: 100%; 
		margin-top: 3em; 
	}
	
	.grid__item { 
		margin: 0 0 20em; 
	}
	
	.grid__item:nth-child(odd) { 
		margin-top: 0; 
	}

	.grid__item-title, .grid__item:nth-child(odd) .grid__item-title { 
		font-size: 4em; 
		text-align: center; 
		width: 100%; 
		padding: 0; 
		top: 115%; 
	}

	span {
		margin-top: 10px;
	}
}

.glitch {
	position: relative;
	width: $glitch-width;
	max-width: 400px;
	height: $glitch-height;
	max-height: calc(400px * 1.25);
	overflow: hidden;
	margin: 0 auto;
}

.glitch:hover {
	cursor: pointer;
}

.glitch__img {
	position: absolute;
	width: 400px;
	height: 400px;
	background: url("../img/img8.jpg") no-repeat 50% 0;
	background-color: $blend-color-1;
	background-size: cover;
	transform: translate3d(0,0,0);
	background-blend-mode: $blend-mode-1;
}

.glitch--style-2 .glitch__img {
	background-image: url("../img/img6.jpg");
}

.glitch--style-3 .glitch__img {
	background-image: url("../img/img5.jpg");
}

.glitch--style-4 .glitch__img {
	background-image: url("../img/img3.jpg");
}

.glitch--style-5 .glitch__img {
	background-image: url("../img/img1.jpg");
}

.glitch--style-6 .glitch__img {
	background-image: url("../img/img9.jpg");
}

.glitch__img:nth-child(n+2) {
	opacity: 0;
}

.glitch:hover .glitch__img:nth-child(n+2) {
	opacity: 1;
}

.glitch:hover .glitch__img:nth-child(2) {
	transform: translate3d(20px, 0, 0);
	animation: glitch-anim-1-horizontal 2.25s infinite linear alternate;
}

.glitch:hover .glitch__img:nth-child(3) {
	transform: translate3d(calc(-1 * 20px), 0, 0);
	animation: glitch-anim-2-horizontal 2.25s infinite linear alternate;
}

.glitch:hover .glitch__img:nth-child(4) {
	transform: translate3d(0, calc(-1 * 2px), 0) scale3d(-1, -1, 1);
	animation: glitch-anim-3-horizontal 2.25s infinite linear alternate;
}

.glitch--vertical:hover .gliltch__img:nth-child(2) {
	transform: translate3d(0, 2px, 0);
	animation: glitch-anim-1-vertical 2.25s infinite linear alternate;
}

.glitch--vertical:hover .gliltch__img:nth-child(3) {
	transform: translate3d(0, calc(-1 * 2px), 0);
	animation: glitch-anim-2-vertical 2.25s infinite linear alternate;
}

.glitch--vertical:hover .gliltch__img:nth-child(4) {
	transform: translate3d(calc(-1 * 2px), 0, 0) scale3d(-1, -1, 1);
	animation: glitch-anim-3-vertical 2.25s infinite linear alternate;
}

.glitch:hover > .glitch__img:nth-child(5) {
	animation: glitch-anim-flash 0.5s steps(1, end) infinite;
}

.footer {
	display: flex;
	font-family: 'Futura', sans-serif;
	color: $accent;
	flex-direction: row;
	box-sizing: border-box;
	align-items: center;
	text-align: center;
	justify-content: center;
	letter-spacing: 2px;
	text-transform: lowercase;
	font-size: 12px;
	z-index: -1;
}

